	module.exports = {"data":{"markdownRemark":{"html":"<p>去る8/26から1週間強、部活の合宿に行ってまいりました。場所は千葉の九十九里浜南端、白子町……そこで1週間丸々練習に励んできたわけです。大学応援団の夏季強化合宿とだけあって、まぁさすがきついことは予想していましたが結構充実した合宿でした。ええ、朝起きて朝練行って飯食って寝て午前練行って飯食って寝て午後練行って飯食って寝て宴会やって酔いつぶれて寝て……とっても充実していましたさ。ｗ っつーわけで疲労回復のため今週のコラムはここまで……さて、これからバイトだ。</p>\n<h2>更新情報</h2>\n<p><a href=\"/category/products/apps\">Software Force</a>にソフトウェアを1つ追加。</p>","frontmatter":{"cover":null,"date":"2002-09-05T00:00:00+09:00","strDate":"2002/9/4","title":"合宿"}}},"pathContext":{"path":"/ja/blog/2002/09/05-合宿/","langKey":"ja"}}

